// ================================================
// 1. HELPERS ‚Äî –¥–æ–ø–æ–º—ñ–∂–Ω—ñ —Ñ—É–Ω–∫—Ü—ñ—ó
// ================================================

function lockScroll() {
  document.body.classList.add('body-no-scroll');
}

function unlockScroll() {
  document.body.classList.remove('body-no-scroll');
}

function showError(inputElement, message) {
  // –î–æ–¥–∞—î–º–æ –∫–ª–∞—Å –ø–æ–º–∏–ª–∫–∏
  inputElement.classList.add('feedback-error');

  // –®—É–∫–∞—î–º–æ –∞–±–æ —Å—Ç–≤–æ—Ä—é—î–º–æ —Ç–µ–∫—Å—Ç –ø–æ–º–∏–ª–∫–∏
  let errorElement = inputElement.parentElement.querySelector(
    '.feedback-error-text'
  );
  if (!errorElement) {
    errorElement = document.createElement('div');
    errorElement.className = 'feedback-error-text';
    inputElement.parentElement.appendChild(errorElement);
  }
  errorElement.textContent = message;
}

function clearError(inputElement) {
  inputElement.classList.remove('feedback-error');

  const errorElement = inputElement.parentElement.querySelector(
    '.feedback-error-text'
  );
  if (errorElement) {
    errorElement.remove();
  }
}

function clearAllErrors() {
  document.querySelectorAll('.feedback-error').forEach(el => {
    clearError(el);
  });
  // –¢–∞–∫–æ–∂ –æ—á–∏—â—É—î–º–æ –º–æ–∂–ª–∏–≤—É –≥–ª–æ–±–∞–ª—å–Ω—É –ø–æ–º–∏–ª–∫—É –≤—ñ–¥ —Å–µ—Ä–≤–µ—Ä–∞
  const serverError = document.querySelector('.server-error-message');
  if (serverError) serverError.remove();
}

// ================================================
// 2. –†–ï–ô–¢–ò–ù–ì (–∑—ñ—Ä–æ—á–∫–∏)
// ================================================

function initRating() {
  const ratingContainer = document.querySelector('.rating');
  if (!ratingContainer) return;

  const stars = ratingContainer.querySelectorAll('.rating-star');
  const hiddenInput = document.getElementById('rating-input');

  if (!stars.length || !hiddenInput) return;

  stars.forEach(star => {
    star.addEventListener('click', () => {
      const value = Number(star.dataset.value);
      hiddenInput.value = value;

      // –ê–∫—Ç–∏–≤—É—î–º–æ –∑—ñ—Ä–∫–∏ –¥–æ –æ–±—Ä–∞–Ω–æ–≥–æ –∑–Ω–∞—á–µ–Ω–Ω—è –≤–∫–ª—é—á–Ω–æ
      stars.forEach(s => {
        const starValue = Number(s.dataset.value);
        s.classList.toggle('active', starValue <= value);
      });
    });
  });
}

// ================================================
// 3. –§–û–†–ú–ê ‚Äî –≤–∞–ª—ñ–¥–∞—Ü—ñ—è + –≤—ñ–¥–ø—Ä–∞–≤–∫–∞
// ================================================

function initFeedbackForm() {
  const form = document.querySelector('.js-feedback-form');
  if (!form) return;

  const nameInput = form.querySelector('input[name="name"]');
  const messageInput = form.querySelector('textarea[name="message"]');
  const ratingInput = document.getElementById('rating-input');
  const ratingBlock = form.querySelector('.rating');

  const submitText = form.querySelector('.js-submit-text');
  const submitLoader = form.querySelector('.js-submit-loader');

  form.addEventListener('submit', async event => {
    event.preventDefault();

    // –û—á–∏—â—É—î–º–æ –≤—Å—ñ –ø–æ–ø–µ—Ä–µ–¥–Ω—ñ –ø–æ–º–∏–ª–∫–∏
    clearAllErrors();

    const name = nameInput.value.trim();
    const descr = messageInput.value.trim();
    const rating = Number(ratingInput.value);

    let isValid = true;

    // –í–ê–õ–Ü–î–ê–¶–Ü–Ø ‚Äî —Ç—É—Ç –º–∞—î –±—É—Ç–∏ —Ç–æ—á–Ω–∞ —Å—Ö–µ–º–∞ –∑ –¥–æ–∫—É–º–µ–Ω—Ç–∞—Ü—ñ—ó API
    // –ó–∞—Ä–∞–∑ –≤–∏–∫–æ—Ä–∏—Å—Ç–æ–≤—É—î–º–æ —Ä–æ–∑—É–º–Ω—ñ –ø—Ä–∏–ø—É—â–µ–Ω–Ω—è (–±–æ —Ç–æ—á–Ω–æ—ó –¥–æ–∫–∏ –Ω–µ –∑–Ω–∞–π—à–ª–∏)
    // –ó–º—ñ–Ω—ñ—Ç—å —Ü—ñ –ø–µ—Ä–µ–≤—ñ—Ä–∫–∏, –∫–æ–ª–∏ –æ—Ç—Ä–∏–º–∞—î—Ç–µ –æ—Ñ—ñ—Ü—ñ–π–Ω—É —Å—Ö–µ–º—É!

    if (!name) {
      showError(nameInput, "–Ü–º'—è –æ–±–æ–≤'—è–∑–∫–æ–≤–µ");
      isValid = false;
    } else if (name.length < 2 || name.length > 50) {
      showError(nameInput, "–Ü–º'—è –ø–æ–≤–∏–Ω–Ω–æ –±—É—Ç–∏ –≤—ñ–¥ 2 –¥–æ 50 —Å–∏–º–≤–æ–ª—ñ–≤");
      isValid = false;
    }

    if (!descr) {
      showError(messageInput, "–ü–æ–≤—ñ–¥–æ–º–ª–µ–Ω–Ω—è –æ–±–æ–≤'—è–∑–∫–æ–≤–µ");
      isValid = false;
    } else if (descr.length < 10 || descr.length > 1000) {
      showError(
        messageInput,
        '–ü–æ–≤—ñ–¥–æ–º–ª–µ–Ω–Ω—è –ø–æ–≤–∏–Ω–Ω–æ –±—É—Ç–∏ –≤—ñ–¥ 10 –¥–æ 1000 —Å–∏–º–≤–æ–ª—ñ–≤'
      );
      isValid = false;
    }

    if (rating < 1 || rating > 5) {
      showError(ratingBlock, '–û–±–µ—Ä—ñ—Ç—å —Ä–µ–π—Ç–∏–Ω–≥ (1‚Äì5 –∑—ñ—Ä–æ–∫)');
      isValid = false;
    }

    if (!isValid) return;

    // –ë–ª–æ–∫—É–≤–∞–Ω–Ω—è –∫–Ω–æ–ø–∫–∏ + –ø–æ–∫–∞–∑ –ª–æ–∞–¥–µ—Ä–∞
    submitText.hidden = true;
    submitLoader.hidden = false;
    form.querySelector('.feedback-submit-btn').disabled = true;

    try {
      const response = await fetch(
        'https://sound-wave.b.goit.study/api/feedbacks',
        {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
            Accept: 'application/json',
          },
          body: JSON.stringify({
            name,
            rating,
            descr, // –ø–æ–ª–µ –Ω–∞–∑–∏–≤–∞—î—Ç—å—Å—è descr, –∞ –Ω–µ message!
          }),
        }
      );

      if (!response.ok) {
        let errorMsg = '–ü–æ–º–∏–ª–∫–∞ —Å–µ—Ä–≤–µ—Ä–∞';
        try {
          const errData = await response.json();
          errorMsg = errData.message || errorMsg;
        } catch {}
        throw new Error(errorMsg);
      }

      // –£—Å–ø—ñ—à–Ω–∞ –≤—ñ–¥–ø—Ä–∞–≤–∫–∞
      form.reset();
      ratingInput.value = '0';
      document
        .querySelectorAll('.rating-star')
        .forEach(star => star.classList.remove('active'));

      // –ó–∞–∫—Ä–∏–≤–∞—î–º–æ –º–æ–¥–∞–ª–∫—É
      const backdrop = document.querySelector('.js-feedback-backdrop');
      if (backdrop) {
        backdrop.setAttribute('hidden', '');
      }
      unlockScroll();

      // –û–ø—Ü—ñ–æ–Ω–∞–ª—å–Ω–æ: –ø–æ–≤—ñ–¥–æ–º–ª–µ–Ω–Ω—è –ø—Ä–æ —É—Å–ø—ñ—Ö (–º–æ–∂–Ω–∞ –∑–∞–º—ñ–Ω–∏—Ç–∏ –Ω–∞ toast)
      alert('–î—è–∫—É—î–º–æ –∑–∞ –≤–∞—à –≤—ñ–¥–≥—É–∫!');
    } catch (error) {
      console.error('–ü–æ–º–∏–ª–∫–∞ –≤—ñ–¥–ø—Ä–∞–≤–∫–∏ –≤—ñ–¥–≥—É–∫—É:', error);

      // –ü–æ–∫–∞–∑—É—î–º–æ –ø–æ–º–∏–ª–∫—É –ø—ñ–¥ —Ñ–æ—Ä–º–æ—é
      const errorDiv = document.createElement('div');
      errorDiv.className = 'server-error-message';
      errorDiv.style.color = '#dc2626';
      errorDiv.style.textAlign = 'center';
      errorDiv.style.marginTop = '16px';
      errorDiv.textContent =
        error.message || '–ù–µ –≤–¥–∞–ª–æ—Å—è –≤—ñ–¥–ø—Ä–∞–≤–∏—Ç–∏ –≤—ñ–¥–≥—É–∫. –°–ø—Ä–æ–±—É–π—Ç–µ —â–µ —Ä–∞–∑.';
      form.appendChild(errorDiv);

      // –ê–≤—Ç–æ–∑–Ω–∏–∫–Ω–µ–Ω–Ω—è —á–µ—Ä–µ–∑ 6 —Å–µ–∫—É–Ω–¥
      setTimeout(() => errorDiv.remove(), 6000);
    } finally {
      submitText.hidden = false;
      submitLoader.hidden = true;
      form.querySelector('.feedback-submit-btn').disabled = false;
    }
  });
}

// ================================================
// 4. –ú–û–î–ê–õ–ö–ê ‚Äî –≤—ñ–¥–∫—Ä–∏—Ç—Ç—è / –∑–∞–∫—Ä–∏—Ç—Ç—è
// ================================================

function initFeedbackModal() {
  const backdrop = document.querySelector('.js-feedback-backdrop');
  const openButton = document.querySelector('.js-open-feedback');
  const closeButton = document.querySelector('.js-feedback-close');

  if (!backdrop || !openButton || !closeButton) return;

  function openModal() {
    backdrop.removeAttribute('hidden');
    lockScroll();
    clearAllErrors(); // –æ—á–∏—â—É—î–º–æ –ø–æ–º–∏–ª–∫–∏ –ø—Ä–∏ –≤—ñ–¥–∫—Ä–∏—Ç—Ç—ñ
  }

  function closeModal() {
    backdrop.setAttribute('hidden', '');
    unlockScroll();
  }

  // –í—ñ–¥–∫—Ä–∏—Ç—Ç—è –ø–æ –∫–Ω–æ–ø—Ü—ñ "Leave feedback"
  openButton.addEventListener('click', openModal);

  // –ó–∞–∫—Ä–∏—Ç—Ç—è –ø–æ –∫–Ω–æ–ø—Ü—ñ ‚úï
  closeButton.addEventListener('click', closeModal);

  // –ó–∞–∫—Ä–∏—Ç—Ç—è –ø–æ –∫–ª—ñ–∫—É –Ω–∞ —Ñ–æ–Ω (backdrop)
  backdrop.addEventListener('click', event => {
    if (event.target === backdrop) {
      closeModal();
    }
  });

  // –ó–∞–∫—Ä–∏—Ç—Ç—è –ø–æ Escape
  document.addEventListener('keydown', event => {
    if (event.key === 'Escape' && !backdrop.hasAttribute('hidden')) {
      closeModal();
    }
  });
}

// ================================================
// 5. –Ü–ù–Ü–¶–Ü–ê–õ–Ü–ó–ê–¶–Ü–Ø ‚Äî –∑–∞–ø—É—Å–∫–∞—î–º–æ –≤—Å–µ –ø—Ä–∏ –∑–∞–≤–∞–Ω—Ç–∞–∂–µ–Ω–Ω—ñ —Å—Ç–æ—Ä—ñ–Ω–∫–∏
// ================================================

document.addEventListener('DOMContentLoaded', () => {
  initFeedbackModal(); // –º–æ–¥–∞–ª–∫–∞ + –≤—ñ–¥–∫—Ä–∏—Ç—Ç—è/–∑–∞–∫—Ä–∏—Ç—Ç—è
  initRating(); // –∑—ñ—Ä–æ—á–∫–∏
  initFeedbackForm(); // —Ñ–æ—Ä–º–∞ + –≤–∞–ª—ñ–¥–∞—Ü—ñ—è + –≤—ñ–¥–ø—Ä–∞–≤–∫–∞
});
